var details = '../components/detail/';
export default new Router({
  mode: 'history',
  routes: [
    // 首页
    {path: '/',redirect:'index.html',component: resolve => require(['../components/index.vue'],resolve)},
    {path: '/index.html',name: 'index',component: resolve => require(['../components/index.vue'],resolve)},
    // git
    {path: '/git_use',name: 'git_use',component: resolve => require(['../components/detail/git_use.vue'],resolve)},
    {path: '/git_morekf',name: 'git_morekf',component: resolve => require(['../components/detail/git_morekf.vue'],resolve)},
    // vue
    {path: '/vue_host',name: 'vue_host',component: resolve => require(['../components/detail/vue_host.vue'],resolve)},
    {path: '/vue_font',name: 'vue_font',component: resolve => require(['../components/detail/vue_font.vue'],resolve)},
    {path: '/vue_qingqiu',name: 'vue_qingqiu',component: resolve => require(['../components/detail/vue_qingqiu.vue'],resolve)},
    // JS
    {path: '/mobile_shipei',name: 'mobile_shipei',component: resolve => require(['../components/detail/mobile_shipei.vue'],resolve)},
    // 混合
    {path: '/ios_keybord',name: 'ios_keybord',component: resolve => require(['../components/detail/ios_keybord.vue'],resolve)},
    
  ]
})



